<template>
	<div>
		<!-- 	<div class="btn">
			<el-button type="primary" @click="addDoubtable">添加可疑人脸</el-button>
		</div> -->

		<el-dialog title="添加可疑人脸" :visible.sync="dialogDoubtable" :close-on-click-modal="false">
			<div class="box">
				<el-form :model="form" label-width="100px">
					<el-form-item label="姓名">
						<el-input v-model="form.name" placeholder="请输入姓名"></el-input>
					</el-form-item>
					<el-form-item label="身份证号">
						<el-input v-model="form.number" placeholder="请输入身份证号"></el-input>
					</el-form-item>
					<div class="tips">
						<p><span>提示：</span>如有人脸照片会覆盖掉原有的人脸照片！</p>
					</div>
					<el-form-item label="上传人脸图片">
						<el-upload action="https://upload-z2.qiniup.com" ref="upload" :limit="1" :before-upload="beforeAvatarUpload"
						 :auto-upload="false" :on-success="handleAvatarSuccess" :on-remove="handleRemove" :on-exceed="handleExceed"
						 :on-change="handleChange" :data="imgData">
							<el-button size="small" type="primary">选择图片</el-button>
						</el-upload>
						<div v-if="hasNewImage" style="color: red; font-size: 12px;">* 点击文件名可删除所选图片</div>

						<div class="up-img" v-if="old_href">
							<span style="display: flex;justify-items: center;color: #409eff;">原人脸图片</span>
							<img class="pic-box" :src="old_href">
						</div>
						<div class="up-img" v-if="form.href">
							<span style="display: flex;justify-items: center;color: #67C23A;">新人脸图片</span>
							<img class="pic-box" :src="form.href">
						</div>
						<div class="up-img" v-else>
							<img class="pic-box" :src="change_href">
						</div>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="newDoubtablet">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>

		<el-table :data="tableDate">
			<el-table-column prop="id" label="ID" align="center"></el-table-column>
			<el-table-column prop="danger.name" label="姓名" align="center"></el-table-column>
			<el-table-column prop="danger.number" label="身份证号" align="center"></el-table-column>
			<el-table-column prop="danger.href" label="照片" align="center">
				<template slot-scope="scope">
					<el-popover placement="top-start" title="" trigger="click">
						<img :src="scope.row.danger.href" style="max-width:800px;max-height:800px;" />
						<img slot="reference" :src="scope.row.danger.href" style="max-width:180px;max-height:80px;">
					</el-popover>
				</template>
			</el-table-column>
			<el-table-column prop="log.image" label="抓拍照片" align="center">
				<template slot-scope="scope">
					<el-popover placement="top-start" title="" trigger="click">
						<img :src="scope.row.log.image" style="max-width:800px;max-height:800px;" />
						<img slot="reference" :src="scope.row.log.image" style="max-width:180px;max-height:80px;">
					</el-popover>
				</template>
			</el-table-column>
			<el-table-column prop="score" label="相似度" align="center">
			</el-table-column>
			<el-table-column prop="address" label="抓拍地点" align="center">
			</el-table-column>
			<el-table-column prop="log.timestamp" label="抓拍时间" align="center">
			</el-table-column>
			
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<el-dialog :visible.sync="dialogDel" title="删除年级" width="20%" align="center" :close-on-click-modal="false">
			<div style="font-size: 20px; margin-bottom: 30px;">是否删除该可疑人员</div>
			<span>
				<el-button type="primary" @click="toDel">删除</el-button>
				<el-button type="danger" @click="dialogDel = false">取消</el-button>
			</span>
		</el-dialog>

		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-sizes="[10, 20, 30, 40, 50]"
			 :page-size="pageSize" layout="sizes, prev, pager, next, jumper" @size-change="handleSizeChange" :total="totalPage">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import API from '@/api/index.js'
	import md5 from 'blueimp-md5'
	import axios from 'axios'
	import DATE from '@/utils/date.js'

	export default {
		name: 'gradems',
		data() {
			return {
				dialogDoubtable: false,
				// 上传人脸
				imgData: {
					key: '',
					token: ''
				},
				fileName: '',
				suffix: '',
				qiniuaddr: "https://tu.fengniaotuangou.cn", // 七牛云图片外链地址
				form: {
					href: '',
					name: '',
					number: ''
				},
				old_href: '', // 原人脸图片
				change_href: '',
				hasNewImage: false,

				tableDate: [],
				id: '',
				dialogDel: false,
				currentPage: 1,
				pageSize: 10,
				totalPage: 0
			}
		},
		mounted() {
			this.getDangerLogs();
			this.getQiniuToken();
		},
		methods: {
			getDangerLogs() {
				var self = this;
				API.dangerLogs(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
					self.tableDate.forEach(item => {
						item.log.timestamp = DATE.formatTime(item.log.timestamp, 'Y-M-D h:m:s');
					})
				})
			},
			// 更换人脸
			addDoubtable() {
				var self = this;
				self.dialogDoubtable = true;
				self.form = {
					href: '',
					name: '',
					number: ''
				};
				self.change_href = '';
			},

			// 人脸信息
			handleChange(file) {
				var self = this;
				self.change_href = URL.createObjectURL(file.raw);
				self.hasNewImage = true;
			},
			handleRemove(file) {
				var self = this;
				self.change_href = '';
				self.hasNewImage = false;
			},
			beforeAvatarUpload(file) {
				var self = this;
				// self.familyForm.href = file.name;
				self.fileName = md5(file.name);
				self.suffix = file.name.substring(file.name.lastIndexOf('.') + 1);
				self.imgData.key = `tmp_${self.fileName}.${self.suffix}`
			},
			newDoubtablet() {
				var self = this;
				if (self.change_href === '') {
					self.form.href = self.old_href;
					API.dangerFace(self.form).then(res => {
						self.$message.success('上传成功');
						self.currentPage = 1;
						self.form.href = '';
						self.dialogDoubtable = false;
						API.dangerFaces(self.currentPage).then(res => {
							self.tableDate = res.data;
							self.totalPage = res.total;
						})
					})
				} else {
					this.$refs.upload.submit();
				}
			},
			handleAvatarSuccess(res, file) {
				var self = this;
				file.url = `${self.qiniuaddr}/${res.key}`;
				self.form.href = file.url;
				API.dangerFace(self.form).then(res => {
					self.$message.success('上传成功');
					self.currentPage = 1;
					self.dialogStudent = true;
					API.dangerFaces(self.currentPage).then(res => {
						self.tableDate = res.data;
						self.totalPage = res.total;
					})
					self.$refs.upload.clearFiles()
					self.form.href = '';
					self.change_href = '';
					self.old_href = '';
					self.imgData.key = '';
					self.dialogDoubtable = false;
				})
			},
			handleExceed(file, fileList) { //图片上传超过数量限制
				var self = this;
				self.$message.error('上传图片不能超过1张!重新上传');
				self.$refs.upload.clearFiles()
				self.form.href = '';
				self.imgData.key = '';
			},
			// 操作
			handleDel(index, row) {
				var self = this;
				self.id = row.id;
				self.dialogDel = true;
			},

			toDel() {
				var self = this;
				API.delDangerFace(self.id).then(res => {
					self.$message.success('删除成功')
					self.dialogDel = false;
					self.getDangerFaces();
					self.currentPage = 1;
				})
			},

			getQiniuToken() {
				var self = this;
				axios.get('https://api.fengniaotuangou.cn/api/upload/token').then(res => {
					self.imgData.token = res.data.uptoken;
				})
			},

			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.currentPage = val;
				API.dangerLogs(val, self.pageSize).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
					self.tableDate.forEach(item => {
						item.log.timestamp = DATE.formatTime(item.log.timestamp, 'Y-M-D h:m:s');
					})
				})
			},
			// 每页显示条数
			handleSizeChange(val) {
				var self = this;
				self.pageSize = val;
				API.dangerLogs(self.currentPage, val).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
		}
	}
</script>

<style scoped>
	.upload-btn {
		margin-top: 10px;
	}

	.up-img {
		display: inline-block;
		margin: 0 50px;
	}

	.pic-box {
		max-width: 100%;
		height: 200px;
	}

	.tips {
		margin-bottom: 20px;
		color: red;
	}
</style>
